import { LoadingController, ToastController } from '@ionic/angular';
import { Component, OnInit } from '@angular/core';
import { AngularFireAuth } from '@angular/fire/auth';
import { AngularFirestore, AngularFirestoreDocument, AngularFirestoreCollection } from '@angular/fire/firestore';
import { Observable, } from 'rxjs'
import { map } from 'rxjs/operators';
import { ActivatedRoute, Router, NavigationExtras } from '@angular/router'
export interface Item { name: string; }

interface Post {
  title: string;
  content: string;
}


@Component({
  selector: 'app-tab4',
  templateUrl: './tab4.page.html',
  styleUrls: ['./tab4.page.scss'],
})
export class Tab4Page implements OnInit {

  postsCol: AngularFirestoreCollection<Post>;
  posts: any;
  title: string;
  content: string;
  postDoc: AngularFirestoreDocument<Post>;
  post: Observable<Post>;



  userId = '';
  loading: any;
 
  

  constructor(
    public loadingController: LoadingController,
    public router: Router,
    private route: ActivatedRoute,

    public afAuth: AngularFireAuth,
    private firestore: AngularFirestore,
    public toastController: ToastController


  ) {
    this.presentToast('constructor')



  }

  async GetPrivateInfo() {

// get document
    const docRef = this.firestore.collection('privateassos').doc(this.userId);
    docRef.get().pipe(
      map(doc => doc.data())
    )
      .subscribe(data => {
        // do stuff with document
        console.log(data)
        console.log(data.name)
        this.presentToast('début recup doc')
        if(data.name){this.dataUser.nom = data.name;}
        if(data.AssoDescription){this.dataUser.description= data.AssoDescription;}
        if(data.AssoTel){this.dataUser.tel= data.AssoTel;}
        if(data.AssoAdress){this.dataUser.adresse= data.AssoAdress;}
        if(data.AssoSIRET){this.dataUser.siret= data.AssoSIRET;}
        if(data.logo){this.dataUser.logocrop= data.logo;}
        this.loading.dissmiss
        this.presentToast('fin recup doc')
    
        this.loading.dismiss();
       
        
      })

    //this.postDoc = this.firestore.doc<Post>("privateassos/LA");
    //this.post = this.postDoc.valueChanges();

    
  }

  ngOnInit() {
    this.presentToast('OnInit')
    this.presentLoading('Pollénisation du profil')
    //recup info auth
    //this.presentLoading('Pollénisation du profil');
    this.afAuth.authState.subscribe(auth => {
      this.userId = auth.uid;
      console.log("userID:   " + this.userId)
      this.dataUser.email = auth.email;
      this.GetPrivateInfo()

    });
    


  }

  // data to pass to edit page
  dataUser = {
    logocrop: 'n',
    nom: 'n',
    description: 'n',
    tel: 'n',
    email: 'n',
    adresse: 'n',
    siret: 'n',
    logo:'n'
  };
  // go to edit and pass dataUser
  Goedit() {
    this.presentToast('goedit')
    let navigationExtras: NavigationExtras = {
      state: {
        dataUser: this.dataUser
      }
    }
    this.router.navigate(['asso-edit-profile'], navigationExtras);
  }

  logout() {
    this.afAuth.signOut();
    this.router.navigate(['welcome']);
  }




// ALERTS //////////
  async presentLoading(message: string){
    this.loading = await this.loadingController.create({message})
    return this.loading.present();
  }
  async presentToast(message: string) {
    const toast = await this.toastController.create({
      duration: 2000,
      message
    });
    toast.present();
  }
 


}
